<template>
	<!-- 双列商品 -->
	<view class="page biserialParent">
		<view class="biserialbox">
			<view class="goodsList">
				<view class="everyGoods" @click="goToGoodsDetails(item)" v-if="index%2==0"
				 v-for="item,index in goodsList" :key='index'>
					 <view class="identification" :class="isEnterprise?'active':''"></view>
					<view class="itemImg">
						<image :src="imgBaseUrls+(item.pictureName?item.pictureName:item.imgUrl)" ></image>
					</view>
					<view class="goodsTitle ellipsis-two">
						{{item.name}}
					</view>
					<view class="goodsPrice">
						<text class="currency">¥</text>
						<text class="price">{{item.minPrice?item.minPrice:item.sellingPrice}}</text>
						<text class="decoration" v-if="item.defaultPrice && item.minPrice>item.defaultPrice">¥{{item.defaultPrice}}</text>
					</view>
					<view class="receiveBtn" :class="isEnterprise?'active':''">
						免费领
					</view>
				</view>
			</view>
			<view class="goodsList rightGoodsList">
				<view class="everyGoods" @click="goToGoodsDetails(item)" v-if="index%2==1"
				 v-for="item,index in goodsList" :key='index'>
					<view class="identification" :class="isEnterprise?'active':''"></view>
					<view class="itemImg">
						<image :src="imgBaseUrls+(item.pictureName?item.pictureName:item.imgUrl)" ></image>
					</view>
					<view class="goodsTitle ellipsis-two">
						{{item.name}}
					</view>
					<view class="goodsPrice">
						<text class="currency">¥</text>
						<text class="price">{{item.minPrice?item.minPrice:item.sellingPrice}}</text>
						<text class="decoration" v-if="item.defaultPrice && item.minPrice>item.defaultPrice">¥{{item.defaultPrice}}</text>
					</view>
					<view class="receiveBtn" :class="isEnterprise?'active':''">
						免费领
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: ["goodsList"],
		data() {
			return {
				imgBaseUrls: "",
				isEnterprise:false
			}
		},
		mounted() {
			this.isEnterprise = this.$Route.query.enterprise?true:false;
			this.imgBaseUrls = this.$config.imgBaseUrl
		},
		methods:{
			goToGoodsDetails(item){
				this.$Router.push({path:'/pages/goodsDetails/index',query:{goodsId:item.id?item.id:item.itemId}})
			}
		}
	}
</script>

<style>
	.biserialParent {
		width: 100%;
		padding: 0 30rpx;
	}

	.biserialbox {
		width: 100%;
		display: flex;
		justify-content: space-between;
	}

	.goodsList {
		width: 49.3%;
		height: auto;
	}

	.everyGoods {
		width: 100%;
		height: auto;
		background: #FFFFFF;
		padding-bottom: 16rpx;
		border-radius: 16rpx;
		overflow: hidden;
		position: relative;
	}
	
	.identification{
		width: 108rpx;
		height: 36rpx;
		background: url(http://beifa-mall.oss-cn-shanghai.aliyuncs.com/appletsImages/giftGoods-arrwo.png) no-repeat;
		background-size: 100% 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 10;
	}
	
	.identification.active{
		background: url(http://beifa-mall.oss-cn-shanghai.aliyuncs.com/appletsImages/memberArrow.png) no-repeat;
		background-size: 100% 100%;
	}
	
	.itemImg {
		width: 100%;
		height: 334rpx;
	}
	.itemImg image{
		width: 100%;
		height: 100%;
	}
	.goodsTitle {
		width: 100%;
		padding: 0 24rpx;
		margin-top: 24rpx;
		color: #333;
		font-size: 24rpx;
	}

	.goodsPrice {
		padding: 0 24rpx;
		height: 45rpx;
		line-height: 45rpx;
		margin-top: 16rpx;
	}

	.currency {
		font-size: 24rpx;
		color: #FF1558;
	}

	.price {
		font-size: 32rpx;
		color: #FF1558;
		text-decoration: line-through
	}

	.decoration {
		text-decoration: line-through;
		margin-left: 12rpx;
		color: #999;
	}

	.goodsItemTags {
		line-height: 30rpx;
	}

	.biserialTag {
		padding: 0 24rpx;
	}

	.goodsItemTag {
		display: inline-block;
		vertical-align: middle;
		margin-top: 8rpx;
		margin-right: 8rpx;
		color: #FF1558;
		font-size: 24rpx;
	}

	.everyGoods {
		margin-top: 10rpx;
	}

	.superTag {
		padding: 5rpx 8rpx;
		background-color: #242424;
		font-size: 20rpx;
		border-radius: 8rpx;
		color: #FFDCBA;
		margin-top: 8rpx;
		margin-right: 8rpx;
		width: auto;
	}

	.marketIngBox {
		height: 31rpx;
		line-height: 31rpx;
		display: inline-block;
		padding-right: 8rpx;
		background-color: #ffe9d3;
		border-radius: 8rpx;
		font-size: 20rpx;
		margin-right: 8rpx;
		overflow: hidden;
	}

	.marketIngBox image {
		height: 100%;
		width: 70rpx;
	}

	.marketIngBox .verb {
		vertical-align: top;
		color: #242424;
		margin-left: 5rpx;
	}

	.objectPrice {
		vertical-align: top;
		color: #242424;
	}
	.receiveBtn{
		background: #ffdf8c;
		border-radius: 50px;
		font-size: 13px;
		color: #502105;
		display: block;
		position: absolute;
		width: 63px;
		height: 24px;
		line-height: 24px;
		text-align: center;
		right: 12px;
		bottom: 8rpx;
	}
	.receiveBtn.active{
		background: linear-gradient(180deg, #F8D8CA 0%, #F1BD9B 100%);
	}
</style>
